import { atom } from 'recoil';

export const playerAtom = atom<{
  playing: boolean;
  suspended: boolean;
  timeOffset: number;
  animationSpeed: number;
}>({
  key: 'baseMap:player',
  default: {
    playing: false,
    suspended: false,
    timeOffset: 3600 * 6,
    animationSpeed: 500,
  },
});
