import { Facebook, LinkedIn, Reddit, Twitter } from '@mui/icons-material';
import { IconButton, Popover, Stack } from '@mui/material';
import { bindPopover, PopupState } from 'material-ui-popup-state/core';
import { forwardRef } from 'react';
import { openWindow, createUrl } from './utils';

const SOCIAL = {
  twitter: '#00ACEE',
  facebook: '#3b5998',
  linkedin: '#007fb1',
  reddit: '#ff4500',
};

const hashtags = ['avgeeks', 'aviation', 'aboveourheads'];

export const ShareMenu = forwardRef<
  React.ComponentRef<typeof Popover>,
  { state: PopupState }
>(function ShareMenu({ state }, ref) {
  return (
    <Popover
      ref={ref}
      {...bindPopover(state)}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'center',
      }}
      transformOrigin={{ vertical: 'top', horizontal: 'center' }}
    >
      <Stack sx={{ m: 1 }} spacing={1} direction="row">
        <IconButton
          sx={{ color: SOCIAL.twitter }}
          onClick={() => {
            state.close();
            openWindow(
              createUrl('twitter', {
                text: '',
                url: 'https://aboveourheads.github.io',
                hashtags,
              }),
            );
          }}
          size="small"
        >
          <Twitter />
        </IconButton>
        <IconButton
          sx={{ color: SOCIAL.facebook }}
          onClick={() => {
            state.close();
            openWindow(
              createUrl('facebook', {
                text: '',
                url: 'https://aboveourheads.github.io',
                hashtags,
              }),
            );
          }}
          size="small"
        >
          <Facebook />
        </IconButton>
        <IconButton
          sx={{ color: SOCIAL.reddit }}
          onClick={() => {
            state.close();
            openWindow(
              createUrl('reddit', {
                text: '',
                url: 'https://aboveourheads.github.io',
                hashtags,
              }),
            );
          }}
          size="small"
        >
          <Reddit />
        </IconButton>
        <IconButton
          sx={{ color: SOCIAL.linkedin }}
          onClick={() => {
            state.close();
            openWindow(
              createUrl('linkedin', {
                text: '',
                url: 'https://aboveourheads.github.io',
                hashtags,
              }),
            );
          }}
          size="small"
        >
          <LinkedIn />
        </IconButton>
      </Stack>
    </Popover>
  );
});
