import { Close } from '@mui/icons-material';
import {
  Alert,
  Box,
  Button,
  Card,
  CardActionArea,
  CardContent,
  CardHeader,
  Checkbox,
  Collapse,
  DialogActions,
  DialogContent,
  DialogTitle,
  Divider,
  IconButton,
  Link,
  MenuItem,
  TextField,
  Tooltip,
  Typography,
  useTheme,
} from '@mui/material';
import { formatDistance, intlFormat } from 'date-fns';
import { useEffect, useState } from 'react';
import { useDeviceSelectors } from 'react-device-detect';
import { useNavigate } from 'react-router-dom';
import { atom } from 'recoil';
import { AVAILABLE_DATASETS } from '../Loader/datasets';
import { useDatasetLoader, useIsDatasetSelected } from './hooks';
import {
  CoverageIcon,
  DurationIcon,
  FlightIcon,
  SizeIcon,
  StartIcon,
} from './StatisticsIcons';

export const dataloadSettingsDialogAtom = atom<boolean>({
  key: 'dataloadSettings:dialogOpen',
  default: false,
});

export const DataloadSettings: React.FC<{}> = function DataloadSettings() {
  useEffect(() => {
    console.log('Dataload settings mounted !');

    return () => {
      console.log('Dataload settings unmounted !');
    };
  }, []);

  const theme = useTheme();
  const navigate = useNavigate();

  const [{ loadOptions }, { selectDataset }] = useDatasetLoader();

  const [advancedSettingsOpen, setAdvancedSettingsOpen] = useState(false);

  const [selectors] = useDeviceSelectors(window.navigator.userAgent);
  const isFirstLoad = !useIsDatasetSelected();

  const [batchSize, setBatchSize] = useState(loadOptions?.batchSize ?? 2000);
  const [samplingPrecision, setSamplingPrecision] = useState(
    loadOptions?.sampleRate ?? (selectors.isDesktop ? 1 : 8),
  );
  const [limit, setLimit] = useState<null | number>(
    loadOptions?.hardLimit ?? null,
  );

  const [datasetIdx, setSelectedDatasetIdx] = useState<number>(0);

  const warningDisplayed = isFirstLoad && selectors.isMobile;

  const datasets = AVAILABLE_DATASETS;

  return (
    <>
      <DialogTitle>
        {isFirstLoad ? 'Pick a dataset' : 'Data loading settings'}
        {!isFirstLoad && (
          <IconButton
            sx={{ position: 'absolute', right: 8, top: 8 }}
            onClick={() => {
              navigate('/');
            }}
          >
            <Close />
          </IconButton>
        )}
      </DialogTitle>
      <Divider />
      <DialogContent>
        {isFirstLoad && (
          <>
            <Alert severity="info">
              Hello ! This website lets you visualize what's going on in the sky
              above your head. Start by picking one of the available datasets,
              then click on the <em>start</em> button below.
            </Alert>
            {warningDisplayed && (
              <Alert severity="warning" sx={{ mt: 1 }}>
                You're viewing this website on a mobile device and this is not
                ideal. Panning and zooming might be slow, or worse, your browser
                may crash.
                <br />
                Some settings were adjusted to try and provide a better
                experience. If you still encounter issues, try to tweak the
                advanced settings below.
              </Alert>
            )}
          </>
        )}
        <Box
          sx={{
            mt: 1,
            mb: 1,
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-evenly',
            flexWrap: 'wrap',
          }}
        >
          {datasets.map((d, idx) => (
            <Card
              sx={{
                m: 1,
                borderColor: idx === datasetIdx ? 'info.main' : undefined,
                display: 'flex',
                flexDirection: 'column',
                flexGrow: 1,
                flexShrink: 0,
                flexBasis: 1,
                minWidth: theme.spacing(24),
              }}
              variant="outlined"
              key={idx}
            >
              <CardActionArea
                sx={{
                  display: 'flex',
                  alignItems: 'stretch',
                  justifyContent: 'flex-start',
                  flexGrow: 1,
                  flexShrink: 0,
                  flexDirection: 'column',
                }}
                onClick={() => {
                  setSelectedDatasetIdx(idx);
                }}
              >
                <CardHeader
                  title={d.name}
                  action={
                    <Checkbox
                      color="info"
                      disableRipple
                      checked={idx === datasetIdx}
                    />
                  }
                />
                <CardContent
                  sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    flexGrow: 1,
                    flexShrink: 0,
                  }}
                >
                  <Typography sx={{ mb: 4 }}>{d.description}</Typography>
                  <Box
                    sx={{
                      mt: 'auto',
                      display: 'grid',
                      gridTemplateColumns: `${theme.spacing(4)} 1fr`,
                      gridGap: theme.spacing(1),
                      gridRowGap: theme.spacing(2),
                      mb: -1,
                    }}
                  >
                    <Tooltip title="Coverage">
                      <CoverageIcon
                        fontSize="small"
                        sx={{ gridColumn: 1, color: 'text.secondary' }}
                      />
                    </Tooltip>
                    <Box sx={{ gridColumn: 2 }}>
                      <Typography variant="subtitle2">{d.coverage}</Typography>
                    </Box>

                    <Tooltip title="Start">
                      <StartIcon
                        fontSize="small"
                        sx={{ gridColumn: 1, color: 'text.secondary' }}
                      />
                    </Tooltip>
                    <Box sx={{ gridColumn: 2 }}>
                      <Typography variant="subtitle2">
                        {intlFormat(d.start, {
                          year: 'numeric',
                          month: 'numeric',
                          day: 'numeric',
                          hour: 'numeric',
                          minute: 'numeric',
                          timeZoneName: 'short',
                          timeZone: 'UTC',
                        })}
                      </Typography>
                    </Box>

                    <Tooltip title="duration">
                      <DurationIcon
                        fontSize="small"
                        sx={{ gridColumn: 1, color: 'text.secondary' }}
                      />
                    </Tooltip>
                    <Box sx={{ gridColumn: 2 }}>
                      <Typography variant="subtitle2">
                        {formatDistance(0, d.duration * 1000)}
                      </Typography>
                    </Box>

                    <Tooltip title="Download size">
                      <SizeIcon
                        fontSize="small"
                        sx={{ gridColumn: 1, color: 'text.secondary' }}
                      />
                    </Tooltip>
                    <Box sx={{ gridColumn: 2 }}>
                      <Typography variant="subtitle2">
                        {Math.floor(d.size / 1024)} MB
                      </Typography>
                    </Box>

                    <Tooltip title="Number of flights">
                      <FlightIcon
                        fontSize="small"
                        sx={{ gridColumn: 1, color: 'text.secondary' }}
                      />
                    </Tooltip>
                    <Box sx={{ gridColumn: 2 }}>
                      <Typography variant="subtitle2">
                        {d.trajectories > 1000
                          ? `${Math.floor(d.trajectories / 1000)}k`
                          : d.trajectories}{' '}
                        flights
                      </Typography>
                    </Box>
                  </Box>
                </CardContent>
              </CardActionArea>
            </Card>
          ))}
        </Box>
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'flex-end',
          }}
        >
          <Link
            component="button"
            color="text.secondary"
            onClick={() => {
              setAdvancedSettingsOpen((prev) => !prev);
            }}
          >
            Advanced settings
          </Link>
        </Box>

        <Collapse in={advancedSettingsOpen}>
          <TextField
            id="sampleFrequency"
            margin="normal"
            select
            fullWidth
            label="Sampling frequency"
            value={samplingPrecision}
            size="small"
            helperText={
              <>
                Each trajectory has a datapoint every 4 seconds. Since we're
                loading a full day of data, this represents a large number of
                datapoints. The large number of trajectory points has a large
                impact on performance.
                <br />
                This setting will only draw a subset of available datapoints.
                For instance, with a sampling frequency of 4, only 1 out of 4
                datapoints will be drawn.
                <br />
                When undersampling, trajectories will be less precise and more
                angular.
              </>
            }
            onChange={(ev) => {
              const newValue = ev.target.value;
              setSamplingPrecision(parseInt(newValue, 10));
            }}
          >
            {[1, 2, 3, 4, 5, 8, 10, 15].map((value) => {
              return (
                <MenuItem key={value} value={value}>
                  {value === 1 ? 'Every datapoint' : `1 out of ${value}`}
                </MenuItem>
              );
            })}
          </TextField>
          <TextField
            id="limit"
            margin="normal"
            value={limit === null ? 'Unlimited' : limit}
            select
            fullWidth
            size="small"
            label="Flight count hard limit"
            helperText={
              <>
                This settings will introduce a hard limit to the number of
                trajectories loaded.
              </>
            }
            onChange={(ev) => {
              const newValue = ev.target.value;

              if (!newValue || newValue === 'Unlimited') {
                setLimit(null);
                return;
              }

              setLimit(parseInt(newValue, 10));
            }}
          >
            {['Unlimited', 500, 1000, 2000, 4000, 6000].map((value) => (
              <MenuItem key={value} value={value ?? 'Unlimited'}>
                {value}
              </MenuItem>
            ))}
          </TextField>
          <TextField
            id="batchSize"
            margin="normal"
            select
            size="small"
            fullWidth
            label="Batch size"
            value={batchSize}
            helperText={
              <>
                When loaded, flights are processed and sent to the GPU in batch.
                Adjusting the batch size might provide a better experience.
              </>
            }
            onChange={(ev) => {
              const newValue = ev.target.value;
              setBatchSize(parseInt(newValue, 10));
            }}
          >
            {[250, 500, 750, 1000, 1500, 2000, 4000].map((value) => {
              return (
                <MenuItem key={value} value={value}>
                  {value} flights
                </MenuItem>
              );
            })}
          </TextField>
        </Collapse>
      </DialogContent>
      <Divider />
      <DialogActions>
        {!isFirstLoad && (
          <Button
            color="inherit"
            onClick={() => {
              navigate('/');
            }}
          >
            Close
          </Button>
        )}
        <Button
          variant="outlined"
          color="inherit"
          onClick={() => {
            const selectedDataset = AVAILABLE_DATASETS[datasetIdx];
            const dataset = {
              name: selectedDataset.name,
              url: selectedDataset.url,
              duration: selectedDataset.duration,
              includesDepDest: !!selectedDataset.includesDepDest,
              start: selectedDataset.start,
              trajectoryCount: selectedDataset.trajectories,
            };
            selectDataset({
              dataset,
              loadOptions: {
                batchSize,
                hardLimit: limit,
                sampleRate: samplingPrecision,
              },
            });
            navigate('/');
          }}
        >
          {isFirstLoad ? 'Start' : 'Reload'}
        </Button>
      </DialogActions>
    </>
  );
};

export default DataloadSettings;
