import { useState, useEffect, useRef } from 'react';

import {
  AppBar,
  Slider,
  IconButton,
  Fab,
  Box,
  Stack,
  useTheme,
  useMediaQuery,
  Paper,
  styled,
  Typography,
} from '@mui/material';

import { useRecoilState } from 'recoil';
import { useThrottle, useRafLoop } from 'react-use';
import { SkipNext, SkipPrevious, PlayArrow, Pause } from '@mui/icons-material';
import { useDatasetLoader } from '../Loader';

import { PlaybackControls } from './PlaybackControls';
import { ShareButton } from './ShareButton';
import { playerAtom } from './state';
import { MapKey } from './MapKey';
import { useIsMobile } from '../utils';

export const PlayerControlMainPanel: React.FC<{}> =
  function PlayerControlMainPanel() {
    const theme = useTheme();
    const { mediaQuery: isMobile } = useIsMobile();

    /**
     * This is the main piece of state that will be updated on each
     * animation loop.
     */
    const [playerState, setPlayerState] = useRecoilState(playerAtom);
    const lastLoopTsRef = useRef<null | number>(null);

    const [{ metadata: datasetMetadata, loading, error }] = useDatasetLoader();

    /**
     * This piece of state tracks the value of the slider
     * when moving it manually.
     *
     * When the slider is released, update the main piece of state.
     */
    const [sliderLocalValue, setSliderLocalValue] = useState(
      playerState.timeOffset,
    );

    /**
     * When the dataset is loading, stop playing.
     *
     * TODO: When the dataset is loaded, reset player state
     */
    useEffect(() => {
      if (loading) {
        setPlayerState((prev) => ({ ...prev, playing: false }));
        return;
      }
    }, [loading, setPlayerState]);

    const timeOffsetToUpdateSlider = useThrottle(playerState.timeOffset, 33);

    useEffect(() => {
      setSliderLocalValue(timeOffsetToUpdateSlider);
    }, [setSliderLocalValue, timeOffsetToUpdateSlider]);

    const loopLength = datasetMetadata?.duration ?? 3600 * 24;

    /**
     * This is the main animation loop
     */
    const [pause, play, isRafLoopActive] = useRafLoop((time) => {
      if (lastLoopTsRef.current === null) {
        lastLoopTsRef.current = time;
        return;
      }

      const msSinceLastRender = time - lastLoopTsRef.current;
      lastLoopTsRef.current = time;

      setPlayerState((t) => {
        const nextOffset =
          (t.timeOffset +
            (msSinceLastRender / 1000) * playerState.animationSpeed) %
          loopLength;

        if (t.suspended) {
          return t;
        }

        return {
          ...t,
          timeOffset: nextOffset,
        };
      });
    }, false);

    /**
     * Start/stop the animation loop when state.playing changes
     */
    useEffect(() => {
      const animationRunning = isRafLoopActive();

      if (playerState.playing && !playerState.suspended) {
        if (!animationRunning) {
          console.log('Starting playback');
          lastLoopTsRef.current = null;
          play();
          return;
        }
      }

      if (!playerState.playing || playerState.suspended) {
        if (animationRunning) {
          console.log('Pausing playback');
          lastLoopTsRef.current = null;
          pause();
          return;
        }
      }
    }, [
      lastLoopTsRef,
      play,
      pause,
      isRafLoopActive,
      playerState.playing,
      playerState.suspended,
    ]);

    const isDataLoading = loading || !!error;

    return (
      <PlayerContainer>
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignSelf: 'center',
            margin: 1,
            flexGrow: 1,
            flexShrink: 1,
            minWidth: 0,
            maxWidth: '100%',
          }}
        >
          <MapKey disabled={isDataLoading} />
          <Slider
            sx={{
              maxWidth: `calc(100% - ${theme.spacing(4)})`,
              alignSelf: 'center',
              height: 4,
              '& .MuiSlider-thumb': {
                width: 8,
                height: 8,
                transition: '0.3s cubic-bezier(.47,1.64,.41,.8)',
                '&:before': {
                  boxShadow: '0 2px 12px 0 rgba(0,0,0,0.4)',
                },
                '&:hover, &.Mui-focusVisible': {
                  boxShadow: `0px 0px 0px 8px ${
                    theme.palette.mode === 'dark'
                      ? 'rgb(255 255 255 / 16%)'
                      : 'rgb(0 0 0 / 16%)'
                  }`,
                },
                '&.Mui-active': {
                  width: 20,
                  height: 20,
                },
              },
              '& .MuiSlider-rail': {
                opacity: 0.28,
              },
            }}
            disabled={isDataLoading}
            min={0}
            max={loopLength - 1}
            value={sliderLocalValue}
            onChange={(ev, newValue) => {
              setPlayerState((prev) => ({ ...prev, suspended: true }));
              setSliderLocalValue(newValue as number);
            }}
            onChangeCommitted={(ev, newValue) => {
              setSliderLocalValue(newValue as number);
              setPlayerState((prev) => ({
                ...prev,
                suspended: false,
                timeOffset: newValue as number,
              }));
            }}
            size="small"
          />

          <Box
            sx={{
              display: 'grid',
              gridTemplateColumns: 'repeat(3, 1fr)',
              alignItems: 'center',
            }}
          >
            <TinyText
              sx={{
                gridRow: 1,
                pl: 2,
                gridColumn: 1,
                mt: -1,
                justifySelf: 'start',
                alignSelf: 'start',
              }}
            >
              {datasetMetadata && dateToDateStringUTC(datasetMetadata.start)}
            </TinyText>
            <Stack
              sx={{
                gridRow: '1 / span 2',
                gridColumn: 2,
                justifySelf: 'center',
                alignSelf: 'center',
              }}
              spacing={1}
              justifyContent="center"
              alignItems="center"
              direction="row"
            >
              <IconButton
                disabled={playerState.timeOffset === 0 || isDataLoading}
                size={isMobile ? 'medium' : 'large'}
                onClick={() => {
                  setPlayerState((prev) => ({ ...prev, timeOffset: 0 }));
                }}
              >
                <SkipPrevious />
              </IconButton>
              <Fab
                color={playerState.playing ? 'secondary' : 'default'}
                disabled={isDataLoading}
                onClick={() => {
                  setPlayerState((prev) => ({
                    ...prev,
                    playing: !prev.playing,
                  }));
                }}
                size={isMobile ? 'medium' : 'large'}
              >
                {playerState.playing ? <Pause /> : <PlayArrow />}
              </Fab>
              <IconButton
                disabled={
                  isDataLoading ||
                  (!playerState.playing &&
                    playerState.timeOffset === loopLength - 1)
                }
                size={isMobile ? 'medium' : 'large'}
                onClick={() => {
                  setPlayerState((prev) => {
                    /**
                     * Loop length is usually a day and that's 60 * 60 * 24 seconds.
                     *
                     * When skipping forward, we need to go to the next time offset on 10 minutes intervals.
                     *
                     * For instance, if we're at 15:03:36, we need to jump to 15:10:00.
                     */

                    let step = 60 * 10; // 10 minutes
                    if (playerState.animationSpeed > 10) {
                      step = 30 * 60;
                    }

                    const currentOffset =
                      Math.floor(prev.timeOffset / step) * step;

                    const nextOffset = currentOffset + step;

                    return {
                      ...prev,
                      timeOffset: Math.min(loopLength - 1, nextOffset),
                    };
                  });
                }}
              >
                <SkipNext />
              </IconButton>
            </Stack>
            <TinyText
              sx={{
                gridRow: 1,
                gridColumn: 3,
                pr: 2,
                mt: -1,
                justifySelf: 'end',
                alignSelf: 'start',
              }}
            >
              {!isMobile && (
                <>
                  {timeOffsetToString(sliderLocalValue, { offsetHours: -5 })}{' '}
                  EST /{' '}
                </>
              )}
              {timeOffsetToString(sliderLocalValue, {
                showSeconds: playerState.animationSpeed < 60,
              })}{' '}
              UTC
            </TinyText>

            <Box
              sx={{
                display: 'flex',
                flexDirection: 'row',
                alignSelf: 'end',
                gridRow: 2,
                gridColumn: 1,
                alignItems: 'start',
                justifySelf: 'start',
              }}
            >
              <PlaybackControls disabled={isDataLoading} />
            </Box>
            <Box
              sx={{
                gridRow: 2,
                gridColumn: 3,
                display: 'flex',
                flexDirection: 'row',
                alignSelf: 'end',
                justifySelf: 'end',
                alignItems: 'end',
              }}
            >
              <ShareButton disabled={isDataLoading} />
            </Box>
          </Box>
        </Box>
      </PlayerContainer>
    );
  };

const PlayerContainer: React.FC<{}> = function PlayerContainer({ children }) {
  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.up('md'));

  if (!isDesktop) {
    return (
      <AppBar
        elevation={0}
        position="sticky"
        sx={{
          display: 'flex',
          flexDirection: 'column',
          bottom: 0,
          flexShrink: 1,
          maxWidth: '100vw',
          minWidth: 0,
        }}
      >
        {children}
      </AppBar>
    );
  }

  return (
    <Paper
      variant="outlined"
      sx={{
        display: 'flex',
        position: 'absolute',
        bottom: theme.spacing(1),
        width: '50%',
        maxWidth: '50%',
        left: '50%',
        transform: 'translate(-50%, 0)',
      }}
    >
      {children}
    </Paper>
  );
};

const TinyText = styled(Typography)({
  fontSize: '0.75rem',
  opacity: 0.6,
  fontWeight: 500,
  letterSpacing: 0.2,
});

function dateToDateStringUTC(date: Date): string {
  const year = date.getUTCFullYear();
  const month = (date.getUTCMonth() + 1).toString().padStart(2, '0');
  const day = date.getUTCDate().toString().padStart(2, '0');

  return `${year}-${month}-${day}`;
}

function timeOffsetToString(
  timeOffset: number,
  {
    showSeconds = false,
    offsetHours = 0,
  }: { showSeconds?: boolean; offsetHours?: number } = {},
): string {
  const hours = (Math.floor(timeOffset / 3600) + offsetHours + 24) % 24;
  const minutes = Math.floor(timeOffset / 60) % 60;
  const seconds = Math.floor(timeOffset) % 60;

  const hoursStr = hours.toString().padStart(2, '0');
  const minutesStr = minutes.toString().padStart(2, '0');

  const strParts = [hoursStr, minutesStr];

  if (showSeconds) {
    strParts.push(seconds.toString().padStart(2, '0'));
  }

  return strParts.join(':');
}
