import { atom, useRecoilValue, useRecoilState } from 'recoil';
import { useMemo, useCallback } from 'react';
import type { TrajectoryItem } from '../scripts/parseTrajectory';
import { fetchNDJSONBatch } from './fetchAsyncIterator';
import { tuple } from '../utils';
import { useWorker } from './useWorker';
import { useHref, useResolvedPath } from 'react-router-dom';
import { v4 } from 'uuid';

export function useDatasetInformation() {
  const [{ loading, error, statistics, metadata }] = useDatasetLoader();

  const r = useMemo(
    () => ({
      loading: loading,
      loaded: loading === false && metadata !== null,
      error: error,
      statistics,
      metadata,
    }),
    [loading, error, metadata, statistics],
  );

  return r;
}

export function useIsDatasetSelected(): boolean {
  const dataloadSettings = useRecoilValue(selectedDatasetAtom);
  return !!dataloadSettings?.dataset;
}

export type DatasetMetadata = {
  name: string;
  url: string | Array<string>;
  includesDepDest: boolean;
  start: Date;
  duration: number;
  trajectoryCount: number;
};

export type DatasetLoadOptions = {
  sampleRate: number;
  hardLimit: number | null;
  batchSize: number;
};

const selectedDatasetAtom = atom<null | {
  dataset: DatasetMetadata;
  loadOptions: DatasetLoadOptions;
}>({
  key: 'loader:selectedDataset',
  default: null,
});

export const datasetAtom = atom<{
  loading: boolean;
  error: null | Error;
  trajectoryChunks: Array<{
    rawItems: Array<Omit<TrajectoryItem, 'timestampOffsets' | 'coordinates'>>;
    positions: Float32Array;
    timestampOffsets: Float32Array;
    startIndices: Uint32Array;
    squawks: Array<number | null>;
  }>;
}>({
  key: 'loader:dataset',
  default: {
    error: null,
    loading: false,
    trajectoryChunks: [],
  },
});

export const dataStatisticsAtom = atom<{
  flights: number;
  drawnDataPoints: number;
  totalDataPoints: number;
  downloadedFlights: number;
  countByAo: { [key: string]: number };
}>({
  key: 'baseMap:dataStatistics',
  default: {
    flights: 0,
    drawnDataPoints: 0,
    totalDataPoints: 0,
    downloadedFlights: 0,
    countByAo: {},
  },
});

export function useDatasetLoader() {
  const [selectedDataset, setSelectedDataset] =
    useRecoilState(selectedDatasetAtom);

  const [data, setData] = useRecoilState(datasetAtom);
  const [statistics, setStatistics] = useRecoilState(dataStatisticsAtom);

  const [, { postMessage, onMessage }] = useWorker();

  /**
   * This will start the loading process of the dataset as defined in selectedDataset
   */
  const startOld = useCallback(() => {
    if (!selectedDataset) {
      return () => {};
    }

    const abortController = new AbortController();

    let aborted = false;
    let done = false;

    async function loadFlights() {
      if (!selectedDataset) {
        return;
      }

      /**
       * Reset state
       */
      setData((prev) => ({
        ...prev,
        loading: true,
        error: null,
        trajectoryChunks: [],
      }));

      /**
       * Reset statistics
       */
      setStatistics({
        countByAo: {},
        downloadedFlights: 0,
        flights: 0,
        drawnDataPoints: 0,
        totalDataPoints: 0,
      });

      let asyncIterator = fetchNDJSONBatch<TrajectoryItem>(
        selectedDataset.dataset.url,
        {
          limit: selectedDataset.loadOptions.hardLimit ?? undefined,
          batchSize: selectedDataset.loadOptions.batchSize,
          abortController,
          onBatchDownloaded: (total) => {
            setStatistics((prev) => ({ ...prev, downloadedFlights: total }));
          },
        },
      );

      const countByAo: { [key: string]: number } = {};
      const samplingRate = selectedDataset.loadOptions.sampleRate;

      /**
       * Now we need to compute buffers for each batch.
       *
       */
      try {
        for await (const trajectoryBatch of asyncIterator) {
          if (aborted) {
            break;
          }

          let sampledIndices = 0;
          let totalIndices = 0;
          /**
           * Count total number of trajectory items
           */

          const expectedPositions: Array<number> = [];
          for (let i = 0; i < trajectoryBatch.length; i++) {
            const t = trajectoryBatch[i];

            // Compute aircraft operator statistics
            if (t.aircraftOperator) {
              if (!countByAo[t.aircraftOperator]) {
                countByAo[t.aircraftOperator] = 0;
              }

              countByAo[t.aircraftOperator]++;
            }

            let expectedIndices = Math.floor(
              t.timestampOffsets.length / samplingRate,
            );

            /**
             * We need to add an extra slot in this case.
             */
            if (t.timestampOffsets.length % samplingRate !== 0) {
              expectedIndices++;
            }

            sampledIndices += expectedIndices;
            expectedPositions[i] = expectedIndices;

            totalIndices += t.timestampOffsets.length;
          }

          const positions = new Float32Array(sampledIndices * 3);
          const timestampOffsets = new Float32Array(sampledIndices);
          const squawks = new Array(trajectoryBatch.length).fill(null);
          const startIndices = new Uint32Array(trajectoryBatch.length);
          startIndices[0] = 0;

          for (let i = 0; i < trajectoryBatch.length; i++) {
            const t = trajectoryBatch[i];
            const numberOfItems = t.coordinates.length;

            // Compute startIndices value
            // We might set an out of bound value for the last iteration of the loop, but that's ok
            let offset = startIndices[i];

            for (let j = 0; j < numberOfItems; j++) {
              if (j % samplingRate !== 0) {
                continue;
              }

              const coordinates = t.coordinates[j];
              const altitudeFt = coordinates[2];
              const altitudeM = altitudeFt / 3.2808;
              positions[offset * 3] = coordinates[0];
              positions[offset * 3 + 1] = coordinates[1];
              positions[offset * 3 + 2] = altitudeM;
              timestampOffsets[offset] = t.timestampOffsets[j];
              squawks[offset] = t.squawks?.[j] ?? null;
              offset++;
            }

            if (offset - startIndices[i] !== expectedPositions[i]) {
              console.log(
                `[WARNING] Flight ${t.callsign}, computed ${
                  offset - startIndices[i]
                } positions, expected ${
                  expectedPositions[i]
                }, initial number of positions = ${
                  t.coordinates.length
                }, remainder = ${t.coordinates.length % samplingRate}`,
              );
            }

            if (i + 1 !== trajectoryBatch.length) {
              startIndices[i + 1] = offset;
            }

            /**
             * Mutate the original object as this is a bit faster and less memory intensive
             */
            // @ts-ignore
            delete t.coordinates;
            // @ts-ignore
            delete t.timestampOffsets;
          }

          if (trajectoryBatch.length > 0) {
            console.log(
              `Creating new trajectoryChunk, with ${startIndices.length} flights`,
            );

            if (aborted) {
              continue;
            }

            setData((prev) => ({
              ...prev,

              trajectoryChunks: prev.trajectoryChunks.concat([
                {
                  rawItems: trajectoryBatch,
                  positions,
                  timestampOffsets,
                  startIndices,
                  squawks,
                },
              ]),
            }));

            setStatistics((prev) => ({
              ...prev,
              drawnDataPoints: prev.drawnDataPoints + sampledIndices,
              totalDataPoints: prev.totalDataPoints + totalIndices,
              flights: prev.flights + trajectoryBatch.length,
            }));
          }
        }

        if (aborted) {
          return;
        }

        setData((prev) => ({
          ...prev,
          loading: false,
        }));

        setStatistics((prev) => ({
          ...prev,
          countByAo,
        }));

        done = true;
      } catch (err: any) {
        if (aborted) {
          return;
        }

        setData((prev) => ({
          ...prev,
          loading: false,
          error: err,
        }));

        done = true;

        console.log('[useDatasetLoader]: An error happened !');
      }
    }

    loadFlights();

    return function abort() {
      if (done) {
        return;
      }

      aborted = true;
      console.log(
        '[useDatasetLoader]: Aborting dataset loading',
        selectedDataset,
      );

      if (abortController) {
        abortController.abort();
      }

      setData((prev) => ({
        ...prev,
        loading: false,
        error: null,
        trajectoryChunks: [],
      }));

      setSelectedDataset(null);
      setStatistics({
        countByAo: {},
        downloadedFlights: 0,
        flights: 0,
        drawnDataPoints: 0,
        totalDataPoints: 0,
      });
    };
  }, [setData, setStatistics, selectedDataset, setSelectedDataset]);

  const rootPath = useResolvedPath('/');

  const start = useCallback(() => {
    if (!selectedDataset) {
      return;
    }

    let running = true;
    const cbId = v4();
    console.log('Starting !', cbId);

    const chunkListener = onMessage('LOAD_CHUNK', (event) => {
      setData((prev) => {
        const trajectoryChunks = prev.trajectoryChunks.slice();
        trajectoryChunks[event.data.idx] = event.data.chunk;

        return {
          ...prev,
          trajectoryChunks,
        };
      });
    });

    const progressListener = onMessage('LOAD_PROGRESS', (event) => {
      const statistics = event.data.statistics;
      setStatistics((prev) => ({ ...prev, ...statistics }));
    });

    const errorListener = onMessage('LOAD_ERROR', (event) => {
      const message = event.data.errorMessage;
      running = false;

      setData((prev) => ({
        ...prev,
        loading: false,
        error: new Error(message),
        trajectoryChunks: [],
      }));
      removeListeners();
    });

    const endListener = onMessage('LOAD_DONE', (event) => {
      running = false;
      setData((prev) => ({ ...prev, loading: false }));
      removeListeners();
    });

    function removeListeners() {
      chunkListener();
      progressListener();
      errorListener();
      endListener();
    }

    setData((prev) => ({
      ...prev,
      loading: true,
      error: null,
      trajectoryChunks: [],
    }));

    setStatistics((prev) => ({
      ...prev,
      countByAo: {},
      downloadedFlights: 0,
      flights: 0,
      drawnDataPoints: 0,
      totalDataPoints: 0,
    }));

    postMessage({
      type: 'LOAD_START',
      metadata: {
        ...selectedDataset.dataset,
        url: (typeof selectedDataset.dataset.url === 'string'
          ? [selectedDataset.dataset.url]
          : selectedDataset.dataset.url
        ).map((u) => `${rootPath.pathname}${u}`),
      },
      loadOptions: selectedDataset.loadOptions,
    });

    return function cancel() {
      console.log('Cancelling', cbId);
      if (running === false) {
        return;
      }

      removeListeners();

      setData((prev) => ({
        ...prev,
        loading: false,
        trajectoryChunks: [],
        error: null,
      }));

      setStatistics((prev) => ({
        ...prev,
        countByAo: {},
        downloadedFlights: 0,
        flights: 0,
        drawnDataPoints: 0,
        totalDataPoints: 0,
      }));

      setSelectedDataset(null);
      postMessage({ type: 'LOAD_ABORT' });
    };
  }, [
    setData,
    setStatistics,
    setSelectedDataset,
    selectedDataset,
    postMessage,
    onMessage,
    rootPath.pathname,
  ]);

  const selectDataset = useCallback(
    (
      opts: null | {
        dataset: DatasetMetadata;
        loadOptions: DatasetLoadOptions;
      },
    ) => {
      setSelectedDataset(opts);
    },
    [setSelectedDataset],
  );

  return tuple(
    {
      loading: data.loading,
      error: data.error,
      chunks: data.trajectoryChunks,
      statistics,
      metadata: selectedDataset?.dataset ?? null,
      loadOptions: selectedDataset?.loadOptions ?? null,
    },
    { start, selectDataset },
  );
}
