import type { ViewStateProps as ViewStateWithInterpolator } from '@deck.gl/core/lib/deck';
import { FlyToInterpolator } from '@deck.gl/core';
import { useRecoilState, atom } from 'recoil';
import { INITIAL_VIEW_STATE } from './constants';
import { useCallback, useMemo } from 'react';
import { tuple } from '../utils';

type RequiredSome<T, U extends keyof T> = Required<Pick<T, U>> & Omit<T, U>;

type StoredViewState = RequiredSome<
  ViewStateWithInterpolator,
  'latitude' | 'longitude' | 'zoom' | 'pitch' | 'bearing'
>;

export const viewStateAtom = atom<StoredViewState>({
  key: 'baseMap:viewState',
  default: INITIAL_VIEW_STATE,
});

type ViewState = Omit<StoredViewState, 'transition' | 'transitionDuration'>;

export function useViewState() {
  const [viewState, setViewState] = useRecoilState(viewStateAtom);

  const flyTo = useCallback(
    (
      viewState: ViewState | ((viewState: ViewState) => ViewState),
      duration: number = 300,
    ) => {
      const transition = new FlyToInterpolator();
      const transitionDuration = duration;

      setViewState((prev) => {
        let newViewState =
          typeof viewState === 'function' ? viewState(prev) : viewState;

        return {
          ...prev,
          ...newViewState,
          transition,
          transitionDuration,
        };
      });
    },
    [setViewState],
  );

  const resetCamera = useCallback(() => {
    setViewState((prev) => ({ ...prev }));
  }, [setViewState]);

  const actions = useMemo(() => ({ flyTo, resetCamera }), [flyTo, resetCamera]);

  return tuple(viewState, actions);
}
