import { Check } from '@mui/icons-material';
import {
  Alert,
  Box,
  Button,
  CardActionArea,
  Collapse,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Divider,
  Link,
  Typography,
} from '@mui/material';
import { useState } from 'react';
import { useCookieConsent } from './analytics';
// @ts-ignore
import previewVideo from './preview.mp4';
import { useIsMobile } from './utils';

export const SplashPage: React.FC<{ onNext: () => void }> =
  function SplashPage({ onNext }) {
    const [{ consent }, cookieConsentActions] = useCookieConsent();
    const [cookieConsentPanelExpanded, setCookieConsentPanelExpanded] =
      useState(false);

    const { ua: isMobile } = useIsMobile();

    return (
      <>
        <DialogTitle>Welcome !</DialogTitle>
        <DialogContent sx={{ flexShrink: 8, pt: 0, pb: 0 }}>
          <Collapse sx={{ pb: 1, pt: 1 }} in={!cookieConsentPanelExpanded}>
            <DialogContentText gutterBottom>
              This website lets you visualize and explore flight tracking
              information.
              {/* Small gif showing capabilities */}
              {/* Mobile warning */}
            </DialogContentText>
            <video
              width="100%"
              muted
              loop
              autoPlay
              disablePictureInPicture
              disableRemotePlayback
              playsInline
              controlsList="nodownload nofullscreen"
              src={previewVideo}
            />

            {isMobile && (
              <Alert severity="warning">
                You're viewing this website on a mobile device and this is not
                ideal. Panning and zooming might be slow, or worse, your browser
                may crash.
              </Alert>
            )}
          </Collapse>
        </DialogContent>
        <Divider />
        {consent === null && (
          <>
            <CardActionArea
              onClick={() => {
                setCookieConsentPanelExpanded((prev) => !prev);
              }}
            >
              <DialogContent sx={{ pt: 1, pb: 1 }}>
                <Typography sx={{ display: 'block' }} variant="caption">
                  This website uses cookies for analytics purposes.{' '}
                  <Link href="#" color="inherit">
                    Read more
                  </Link>
                </Typography>
              </DialogContent>
            </CardActionArea>

            <DialogContent sx={{ flexShrink: 1, pt: 0, pb: 0 }}>
              <Collapse sx={{ pt: 1, pb: 1 }} in={cookieConsentPanelExpanded}>
                <Typography variant="h5" gutterBottom>
                  Cookies we use
                </Typography>
                <Typography variant="h6" gutterBottom>
                  Strictly necessary cookies
                </Typography>
                <DialogContentText>
                  These cookies are necessary for the website to function and
                  cannot be switched off in our systems. They are usually only
                  set in response to actions made by you which amount to a
                  request for services, such as setting your privacy
                  preferences, logging in or filling in forms. You can set your
                  browser to block or alert you about these cookies, but some
                  parts of the site will not then work. These cookies do not
                  store any personally identifiable information.
                </DialogContentText>

                <Divider sx={{ mt: 2, mb: 2 }} />
                <Typography variant="h6" gutterBottom>
                  Performance Cookies
                </Typography>
                <DialogContentText gutterBottom>
                  These cookies allow us to count visits and traffic sources so
                  we can measure and improve the performance of our site. They
                  help us to know which pages are the most and least popular and
                  see how visitors move around the site. All information these
                  cookies collect is aggregated and therefore anonymous. If you
                  do not allow these cookies we will not know when you have
                  visited our site, and will not be able to monitor its
                  performance.
                </DialogContentText>
                <Typography>Tools used:</Typography>
                <ul>
                  <li>Google Analytics</li>
                </ul>
              </Collapse>
            </DialogContent>
          </>
        )}
        <DialogActions>
          {consent === null ? (
            <>
              <Button
                color="inherit"
                variant="outlined"
                onClick={() => {
                  onNext();
                }}
              >
                Reject
              </Button>
              <Button
                variant="contained"
                color="success"
                onClick={() => {
                  cookieConsentActions.accept();
                  onNext();
                }}
              >
                Accept and continue
              </Button>
            </>
          ) : (
            <>
              <Typography sx={{ mr: 1 }} variant="caption">
                <Check fontSize="inherit" color="success" /> Cookies{' '}
                <Link
                  href="#"
                  onClick={() => cookieConsentActions.reset()}
                  color="text.secondary"
                  sx={{
                    textDecorationStyle: 'dashed',
                  }}
                >
                  Reset choice ?
                </Link>
              </Typography>
              <Box sx={{ flexGrow: 1 }} />
              <Button
                color="inherit"
                variant="outlined"
                onClick={() => {
                  // Extend cookie life
                  cookieConsentActions.accept();
                  onNext();
                }}
              >
                Continue
              </Button>
            </>
          )}
        </DialogActions>
      </>
    );
  };
